//frontend/pages/buy.js
import { useEffect, useState } from 'react';
import Navbar from '../components/Navbar';
import ProductCard from '../components/ProductCard';

export default function BuyerDashboard() {
  const [userLoggedIn, setUserLoggedIn] = useState(false);
  const [purchasedProducts, setPurchasedProducts] = useState([]);

  useEffect(() => {
    if (typeof window !== "undefined") {
      const token = localStorage.getItem("token");
      if (token) setUserLoggedIn(true);
    }

    // Fetch only the buyer's purchased posts
    setPurchasedProducts([
      { id:1, title:"ফ্রি ফায়ার আইডি", category:"গেম আইডি", price:15, image:"/images/sample1.jpg" }
    ]);
  }, []);

  if(!userLoggedIn) return <div className="p-8">Login করতে হবে।</div>

  return (
    <div className="bg-gray-100 dark:bg-gray-900 min-h-screen">
      <Navbar />
      <h1 className="text-2xl font-bold p-4">আপনার কেনা পোস্টসমূহ</h1>
      <div className="p-4 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
        {purchasedProducts.map(product => (
          <ProductCard key={product.id} product={product} userLoggedIn={userLoggedIn} />
        ))}
      </div>
    </div>
  );
}
